import styled from 'styled-components'
import { Link as LinkR} from 'react-router-dom'



export const Nav = styled.nav`
 background:#ffffff;
 height:80px;
 display:flex;
 justify-items: center;
 align-items:center;
 font-size:1rem;
 position:sticky;
 top:0;
 z-index:10;
 @media screen and (max-width:960px){
 	transition:0.8s all ease;
 	
 }
`;
export const NavbarContainer = styled.div`
display:flex;
justify-content:space-between;
height:80px;
z-index:1;
width:100%;
padding:0 24px;
max-width:1100px;

`;

export const NavLogo = styled(LinkR)`
color: #19345F;
justify-self:flex-start;
cursor:pointer;
font-size1.5rem;
display:flex;
align-items:center;
margin-left:24px;
text-decoration:none;

@media screen and (max-width:768px){
width:25%;


}
`;

export const MobileIcon = styled.div`
display:none;

@media screen and (max-width:768px){
	display:block;
	position:absolute;
	top:0;
	right:0;
	transform:translate(-100%, 60%);
	font-size:1.8rem;
	cursor:pointer;
	color:  #19345F;

}

`;
export const NavMenu = styled.ul`
display:flex;
align-items:center;
list-style:none;
text-align:center;
margin-right:-22px;


@media screen and (max-width:768px){
	display:none;

}

`

export const NavItem = styled.li`
height:80px;
text-decoration:none;

`

export const NavLinks = styled(LinkR)`
color: #19345F;
font-weight:800;
display:flex;
align-items:center;
text-decoration:none;
padding: 0 1rem;
height:100%;
cursor:pointer;

&.active{
	border-bottom:3px solid  #19345F;
	text-decoration:none;
}

`;
export const NavBtn = styled.nav`
display:flex;
align-items:center;

@media screen and (max-width:768px){
	display:none;

}
`;
/*
export const NavBtns = styled.nav`
display:flex;
align-items:center;

@media screen and (max-width:768px){
	display:none;
}`;*/
export const NavBtnLink = styled(LinkR)`
border-radius:10px;
background:#fff;
white-space:nowrap;
padding:10px 22px;

color: #19345F;
font-weight:800;
font-size:16px;

border:none;
cursor:pointer;
transition:all 0.2s ease-in-out;
text-decoration:none;


&:hover {
	transition:all 0.2s ease-in-out;
	background: #19345F;
	color:#fff;
	font-weight:800;
	text-decoration:none;
}

`;
